<template>
  <!-- 造梦空间 -->
  <div class="page">
    <div class="cont">
      <div class="info">
        <user-info></user-info>
      </div>
      <div class="group pr">
        <div class="chat" @click="goChat">
          <img
            src="../assets/images/dream/chat@2x.png"
            class="space-img space-chat"
          />
        </div>

        <div class="lucky" @click="goLucky">
          <img
            src="../assets/images/dream/lucky_turn@2x.png"
            alt=""
            class="space-img space-lucky"
          />
        </div>
        <div class="game" @click="() => (isModalWaiting = true)">
          <img
            src="../assets/images/dream/game@2x.png"
            alt=""
            class="space-img space-game"
          />
        </div>
      </div>
    </div>

    <Modal class="modal" :visible.sync="isModalWaiting" title="温馨提示">
      <div class="modal-content" slot="content">
        <div class="text mb-24">敬请期待</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          @click="() => (isModalWaiting = !isModalWaiting)"
          src="@/assets/images/common/modal_btn_zdl.png"
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import UserInfo from '@/components/userInfo'

export default {
  components: {
    UserInfo
  },

  data () {
    return {
      isModalWaiting: false
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    goChat () {
      this.$router.push('dialogue')
    },
    goLucky () {
      this.$router.push('luckyTurn')
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  .bg-image("../assets/images/dream/bg","png");

  .cont {
    z-index: 1;
    position: relative;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    .flex_func(column, flex-end, center, nowrap);
    .info {
      width: 100%;
      height: 120px;
      position: fixed;
      top: 0;
      z-index: 4;
      .flex_func(row, space-between, flex-end, nowrap);
      .info-left {
        width: 400px;
        height: 100%;
        .flex_func(column, flex-end, flex-start, nowrap);
      }
    }
    .group {
      width: 100%;
      height: 100%;
      z-index: 1;
      .chat {
        width: 270px;
        height: 415px;
        position: absolute;
        top: 188px;
        left: 320px;
      }

      .lucky {
        width: 225px;
        height: 365px;
        position: absolute;
        top: 510px;
        left: 110px;
      }
      .game {
        width: 259px;
        height: 295px;
        position: absolute;
        bottom: 250px;
        right: 87px;
      }
      .space-img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>



